@import "../../common/colors.scss";
.card-item{
    background-color: $secondary-color;
    cursor: pointer;
    transition: all 0.3s;
    &:hover{
        transform: scale(1.1);
        transition: all 0.3s;
    }
}
.card-top{
    height: 300px;
    img{
        width: 100%;
        height: 100%;
    }
}
.card-info{
    color: $font-primary;
    h4{
        font-size: 22px;
        font-weight: 400;
        margin-bottom: 10px;
    }
}
.card-bottom{
    padding: 20px;
}
